<template>
  <div class="business-rules">

    <!-- 头部 -->
    <div class="rule-name">
      <slot name="header"></slot>
      <el-button size="small" icon="el-icon-plus" @click="newRule">新建</el-button>
    </div>

    <!-- 规则列表 -->
    <el-table
      :data="rules"
      stripe
      size="small"
      v-loading="saving"
      style="width: 100%">

      <el-table-column prop="rid" label="规则ID" align="center" width="130px">
        <template slot-scope="scope">
          <el-input
            v-if="scope.row.unsave"
            v-focus
            v-model="scope.row.rid"
            size="small"
            :class="{ 'is_error': scope.row.isError }"
            placeholder="请输入规则ID"
            @input="getRuleById($event, scope.row)">
          </el-input>
          <span v-else>{{ scope.row.rid }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="name" label="规则名称">
        <template slot-scope="scope">{{ scope.row.name || '-' }}</template>
      </el-table-column>

      <el-table-column prop="desc" label="规则说明">
        <template slot-scope="scope">{{ scope.row.desc || '-' }}</template>
      </el-table-column>

      <el-table-column prop="status" label="状态" align="center" width="100px" v-if="type === ''">
        <template slot-scope="scope">
          {{ scope.row.status || '-' }}
        </template>
      </el-table-column>

      <el-table-column label="操作" align="center" width="130px">
        <template slot-scope="scope">
          <span v-if="scope.row.unsave">
            <el-link icon="el-icon-document-add" @click="addRule(scope.row)">保存</el-link>
          </span>
          <span v-else>
            <el-link type="warning" icon="el-icon-edit-outline" @click="editRule(scope.row)" style="margin-right: 5px;">编辑</el-link>
            <el-popconfirm
              title="是否确认删除？"
              icon="el-icon-warning"
              iconColor="#F56C6C"
              confirmButtonType="danger"
              cancelButtonText="按错了"
              @onConfirm="removeRule(scope.row)">
              <el-link slot="reference" type="danger" icon="el-icon-remove-outline">删除</el-link>
            </el-popconfirm>
          </span>
        </template>
      </el-table-column>
    </el-table>

    <!-- 错误提示 -->
    <div class="rule-id-err-msg">{{ ridErrMsg }}</div>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name: 'business-rules',
  props: {
    type: {
      type: String,
      default: ''
    },
    rules: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      ridErrMsg: '', // 用户行为id错误提示语
      inputTimer: null,
      saving: false
    }
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.querySelector('input').focus()
      }
    }
  },
  methods: {
    // 新建规则
    newRule () {
      this.rules.push({
        rid: '',
        name: '',
        desc: '',
        unsave: true,
        isError: false
      })
    },

    // 保存规则
    addRule (rule) {
      this.validRuleId(rule).then(() => {
        this.setValue(rule, 'unsave', false)
      }).catch(() => {})
    },

    // 编辑规则
    editRule (rule) {
      this.setValue(rule, 'unsave', true)
    },

    // 删除规则
    removeRule (rule) {
      const item = this.rules.findIndex(l => l.rid === rule.rid)
      item >= 0 && this.rules.splice(item, 1)
    },

    // 校验规则id
    validRuleId (rule) {
      return new Promise((resolve, reject) => {
        if (rule.rid.toString().trim() === '') {
          this.ridErrMsg = '保存失败，请正确填写规则ID'
          this.setValue(rule, 'isError', true)
          reject(new Error('规则ID为空'))
          return
        }

        this.ridErrMsg = ''
        this.setValue(rule, 'isError', false)

        this.saving = true
        this.getRuleById(rule.rid, rule)
          .then(res => resolve())
          .catch(error => {
            this.ridErrMsg = '保存失败，请正确填写规则ID'
            this.setValue(rule, 'isError', true)
            reject(error)
          })
          .finally(() => {
            this.saving = false
          })
      })
    },

    // 根据id查规则
    getRuleById (rid, rule) {
      return new Promise((resolve, reject) => {
        clearTimeout(this.inputTimer)
        this.inputTimer = setTimeout(() => {
          if (rid === '') {
            rule.name = ''
            rule.desc = ''
            rule.status = ''
            resolve()
            return
          }

          // todo: 异步检查规则是否存在
          setTimeout(() => {
            const res = {
              rid: '123',
              name: '456',
              desc: '789',
              status: ''
            }
            rule.name = res.name
            rule.desc = res.desc
            rule.status = res.status
            resolve(rule)
          }, 500)
        }, 200)
      })
    },

    // 未保存验证
    validRules () {
      if (this.rules.some(r => r.unsave)) {
        this.ridErrMsg = '有规则尚未保存'
        return false
      }
      return true
    },

    setValue (rule, attr, val) {
      if (attr in rule) {
        rule[attr] = val
      } else {
        Vue.set(rule, attr, val)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.business-rules {
  position: relative;
  margin-bottom: 40px;

  .rule-name {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    font-size: 13px;
    font-weight: bold;
  }
  .el-table {
    font-size: 13px;

    ::v-deep th {
      background-color: #fafafa;
    }

    .el-input {
      width: 100%;
      font-size: 13px;
    }

    .el-link {
      font-size: 13px;
    }
  }

  .sub-bus-rules {
    margin-bottom: 20px;
  }

  .rule-id-err-msg {
    position: absolute;
    right: 20px;
    bottom: -22px;
    font-size: 12px;
    color: #f56c6c;
  }

  ::v-deep .el-input.is_error .el-input__inner {
    border-color: #f56c6c;
  }
}
</style>
